import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";
import { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronLeft,
  faChevronRight,
} from "@fortawesome/free-solid-svg-icons";
import { GetNowPlaying } from "../../Api/GetApi";
import CardPlayingSection from "./CardPlayingSection";

const NowPlayingSection = ({theme}) => {
  const [movieItems, setMovieItems] = useState([]);
  useEffect(() => {
    GetNowPlaying().then((res) => {
      setMovieItems(res);
    });
  }, []);

  return (
    <section>
      <div className="bg-colorone dark:bg-gray-400  w-full h-fit py-5 px-1">
        <div className="text-gray-400 text-[23px] px-5 my-2">
          <h1>Now Playing</h1>
        </div>
        <div className="w-full h-fit relative">
          <div className="hidden md:block">
            <div className="btn_prev_playingnow absolute z-[10] bg-gray-600 hover:bg-gray-700 text-gray-400 hover:text-gray-300 w-[40px] h-[40px] rounded-full flex justify-center items-center duration-300 left-3 top-[40%]">
              <FontAwesomeIcon icon={faChevronLeft} />
            </div>
            <div className="btn_next_playingnow absolute z-[10] bg-gray-600 hover:bg-gray-700 text-gray-400 hover:text-gray-300 w-[40px] h-[40px] rounded-full flex justify-center items-center duration-300 right-3 top-[40%]">
              <FontAwesomeIcon icon={faChevronRight} />
            </div>
          </div>
          <Swiper
            // slidesPerView={1}
            loop={true}
            loopAdditionalSlides={movieItems.length}
            spaceBetween={30}
            grabCursor={true}
            navigation={{
              prevEl: ".btn_prev_playingnow",
              nextEl: ".btn_next_playingnow",
            }}
            width={160}
            modules={[Navigation]}
            className="mySwiper"
          >
            {movieItems.map((movie, i) => {
              return (
                <SwiperSlide key={i}>
                  <CardPlayingSection data={movie} />
                </SwiperSlide>
              );
            })}
          </Swiper>
        </div>
      </div>
    </section>
  );
};

export default NowPlayingSection;
