import { faCircle, faPlay, faStar } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const MovieDetailSection = ({ data }) => {
  const baseUrlImg = process.env.REACT_APP_BASE_URL_IMG;
  return (
    <div className="w-full h-fit bg-colorone px-10">
      <div className="w-full h-[70%]">
        <div className="w-full h-full flex flex-col md:flex-row gap-5 relative">
          <div className="min-w-full md:min-w-[200px]">
            <img
              className="absolute z-10 top-[-150px] left-[50%] translate-x-[-50%] md:top-[-100px] md:left-0 w-[150px] md:w-[200px] md:translate-x-0  rounded-xl"
              src={`${baseUrlImg}${data.poster_path}`}
              alt=""
            />
          </div>
          <div className="w-full flex flex-col gap-3 mt-[70px] md:mt-0">
            <div className="w-full h-fit flex flex-col md:flex-row items-center gap-0 md:gap-3 text-center md:text-start text-colorthree font-dmsans text-[30px]">
              <h2>{data.title}</h2>
              <p className="text-[14px]">
                <i>{`(${data.tagline})`}</i>
              </p>
            </div>
            <div className="w-full h-fit flex flex-wrap justify-center md:justify-start gap-5">
              {data.genres &&
                data.genres.map((genre, i) => {
                  return (
                    <div
                      key={i}
                      className="w-fit h-fit bg-colortwo text-colorthree px-5 rounded-full"
                    >
                      <p>{genre.name}</p>
                    </div>
                  );
                })}
            </div>
            <div className="w-full h-fit justify-center md:justify-start text-gray-500 text-[14px] gap-3 flex items-center">
              <p>{data.release_date}</p>
              <FontAwesomeIcon className="text-[7px]" icon={faCircle} />
              <div className="flex gap-1 items-center">
                <FontAwesomeIcon className="text-amber-600" icon={faStar} />
                <p>{Math.floor(data.vote_average * 10) / 10}</p>
              </div>
            </div>
            <div
              className="w-full h-fit flex justify-center md:justify-start"
            >
              <div className="flex  gap-4 items-center text-gray-900 bg-gray-700 px-16 py-2 rounded-full hover:bg-amber-600 duration-300 cursor-pointer">
                <FontAwesomeIcon icon={faPlay} />
                <p>Play</p>
              </div>
            </div>
          </div>
        </div>
        <div className="text-gray-500 w-full h-fit mt-10 py-10 bg-gray-900">
          <p>{data.overview}</p>
        </div>
      </div>
    </div>
  );
};

export default MovieDetailSection;
