import { faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { useNavigate } from "react-router-dom";
import { faArrowLeft } from "@fortawesome/free-solid-svg-icons";

const Search = ({value}) => {
  const navigate = useNavigate();

  const handleChange = (e) => {
    value(e.target.value)
  }
  
  return (
    <header>
      <div className="w-full h-[80px] px-5 md:px-20 fixed z-20 flex gap-5 items-center">
        <div className="block md:hidden">
          <div onClick={() => navigate('/')} className="w-[30px] h-[30px] bg-amber-600 hover:bg-amber-600 rounded-full flex justify-center items-center cursor-pointer">
            <FontAwesomeIcon icon={faArrowLeft} />
          </div>
        </div>
        <div className="w-full md:w-fit px-5 py-2 h-fit flex gap-3 items-center rounded bg-gray-700">
          <input onChange={handleChange} className="bg-transparent text-colorthree outline-none w-full md:w-[300px]" type="text" />
          <FontAwesomeIcon className="text-gray-500" icon={faMagnifyingGlass} />
        </div>
      </div>
    </header>
  )
}

export default Search