import { logDOM } from "@testing-library/react"
import axios from "axios"

const BaseUrlMovie = process.env.REACT_APP_BASE_URL_MOVIE
const ApiKey = process.env.REACT_APP_API

export const GetPopularApi = async() => {
  const popular = await axios.get(`${BaseUrlMovie}/movie/popular?api_key=${ApiKey}&page=1`)
  return popular.data.results
}

export const GetTrendingApi = async(params) => {
  const trending = await axios.get(`${BaseUrlMovie}/trending/movie/${params}?api_key=${ApiKey}&page=1`)
  return trending.data.results;
}

export const GetNowPlaying = async() => {
  const nowplaying = await axios.get(`${BaseUrlMovie}/movie/now_playing?api_key=${ApiKey}&page=1`)
  return nowplaying.data.results
}

export const GetDetailMovie = async(id) => {
  const detail = await axios.get(`${BaseUrlMovie}/movie/${id}?api_key=${ApiKey}&page=1`)
  return detail.data
}

export const GetDetailCastMovie = async(id) => {
  const cast = await axios.get(`${BaseUrlMovie}/movie/${id}/credits?api_key=${ApiKey}`)
  return cast.data
}

export const GetMovieRecomendations = async(id) => {
  const recomend = await axios.get(`${BaseUrlMovie}/movie/${id}/recommendations?api_key=${ApiKey}`)
  return recomend.data.results;
}

export const GetSearchMovie = async(value) =>{
  try {
    const search = await axios.get(`https://api.themoviedb.org/3/search/movie?api_key=${ApiKey}&query=${value}`)
    return search.data.results
  } catch (error) {
    
  }
}

export const GetDiscover = async(genres) => {
  const discover = await axios.get(
    `${BaseUrlMovie}/discover/movie?api_key=${ApiKey}&sort_by=popularity.desc&page=1&with_genres=${genres}&with_watch_monetization_types=flatrate`
  );
  return discover.data.results
}

export const GetGenres = async() => {
  const Genres = await axios.get(
    `${BaseUrlMovie}/genre/movie/list?api_key=${ApiKey}`
  );
  return Genres.data.genres
}

export const GetImageMovie = async(id) => {
  const img = await axios.get(`${BaseUrlMovie}/movie/${id}/images?api_key=${ApiKey}`)
  return img.data
}