import { useEffect, useState } from "react"
import { GetDetailCastMovie } from "../../Api/GetApi"
import { Swiper, SwiperSlide } from "swiper/react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronLeft, faChevronRight } from "@fortawesome/free-solid-svg-icons";
import { Navigation } from "swiper"
import CardCast from "./CardCast"

const CastDetailSection = ({data}) => {
  const [cast, setCast] = useState([])

  useEffect(() => {
    GetDetailCastMovie(data).then(res => setCast(res))
  }, [data])

  return (
    <div className="w-full h-fit relative bg-colorone">
      <div className="hidden md:block">
        <div className="btn_prev_playingdetail absolute z-[10] bg-gray-600 hover:bg-gray-700 text-gray-400 hover:text-gray-300 w-[40px] h-[40px] rounded-full flex justify-center items-center duration-300 left-3 top-[40%]">
          <FontAwesomeIcon icon={faChevronLeft} />
        </div>
        <div className="btn_next_playingdetail absolute z-[10] bg-gray-600 hover:bg-gray-700 text-gray-400 hover:text-gray-300 w-[40px] h-[40px] rounded-full flex justify-center items-center duration-300 right-3 top-[40%]">
          <FontAwesomeIcon icon={faChevronRight} />
        </div>
      </div>
      <Swiper
        loop={true}
        loopAdditionalSlides={cast.cast && cast.cast.length}
        spaceBetween={30}
        grabCursor={true}
        navigation={{
          prevEl: ".btn_prev_playingdetail",
          nextEl: ".btn_next_playingdetail",
        }}
        width={160}
        modules={[Navigation]}
        className="mySwiper"
      >
        {cast.cast &&
          cast.cast.map((movie, i) => {
            if (movie.profile_path) {
              return (
                <SwiperSlide key={i}>
                  <CardCast data={movie} />
                </SwiperSlide>
              );
            }
          })}
      </Swiper>
    </div>
  );
}

export default CastDetailSection