import { faArrowLeft } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useNavigate } from "react-router-dom";

const Hero = ({data}) => {
  const baseUrlImg = process.env.REACT_APP_BASE_URL_IMG;
  const navigate = useNavigate()
  return(
    <section>
      <div className="w-full h-[600px] relative bg-cover bg-ce" style={{backgroundImage: `url(${baseUrlImg}${data.backdrop_path})`}}>
        <div onClick={() => navigate('/')} className="absolute w-[40px] h-[40px] top-10 left-10 bg-amber-600 hover:bg-amber-600 rounded-full flex justify-center items-center cursor-pointer">
          <FontAwesomeIcon icon={faArrowLeft} />
        </div>
        <div className="absolute w-full h-[40%] bottom-0 bg-gradient-to-t from-colorone to-transparent z-[2]"></div>
        <div className="absolute w-full h-[50px] bottom-0 bg-colorone z-[1] rounded-t-full"></div>
      </div>
    </section>
  )
}

export default Hero