import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";
import { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronLeft,
  faChevronRight,
} from "@fortawesome/free-solid-svg-icons";
import { GetTrendingApi } from "../../Api/GetApi";
import CardMovieTrending from "./CardMovieTrending";

const TrendingSection = () => {
  const [movieItems, setMovieItems] = useState([]);
  const [paramsApi, setParamsApi] = useState('day')

  useEffect(() => {
    GetTrendingApi(paramsApi).then((res) => {
      setMovieItems(res);
    });
  }, [paramsApi]);

  return (
    <section>
      <div className="bg-colorone dark:bg-gray-400 w-full h-fit py-5 px-1">
        <div className="text-gray-400 dark:text-gray-800 flex text-[23px] px-5 my-2 justify-between">
          <h1>Tranding Movies</h1>
          <div className="flex gap-5 items-center pr-3 md:px-10">
            <h1
              onClick={() => setParamsApi("day")}
              className={`${
                paramsApi === "day" ? "bg-amber-600 text-gray-900 px-5 py-1 rounded-full" : ""
              } text-[16px] cursor-pointer`}
            >
              Day
            </h1>
            <h1
              onClick={() => setParamsApi("week")}
              className={`${
                paramsApi === "week" ? "bg-amber-600 text-gray-900 px-5 py-1 rounded-full" : ""
              } text-[16px] cursor-pointer`}
            >
              Week
            </h1>
          </div>
        </div>
        <div className="w-full h-fit relative">
          <div className="hidden md:block">
            <div className="btn_prev_trending absolute z-[10] bg-gray-600 hover:bg-gray-700 text-gray-400 hover:text-gray-300 w-[40px] h-[40px] rounded-full flex justify-center items-center duration-300 left-3 top-[40%]">
              <FontAwesomeIcon icon={faChevronLeft} />
            </div>
            <div className="btn_next_trending absolute z-[10] bg-gray-600 hover:bg-gray-700 text-gray-400 hover:text-gray-300 w-[40px] h-[40px] rounded-full flex justify-center items-center duration-300 right-3 top-[40%]">
              <FontAwesomeIcon icon={faChevronRight} />
            </div>
          </div>
          <Swiper
            // slidesPerView={1}
            loop={true}
            loopAdditionalSlides={movieItems.length}
            spaceBetween={30}
            grabCursor={true}
            navigation={{
              prevEl: ".btn_prev_trending",
              nextEl: ".btn_next_trending",
            }}
            width={160}
            modules={[Navigation]}
            className="mySwiper"
          >
            {movieItems.map((movie, i) => {
              return (
                <SwiperSlide key={i}>
                  <CardMovieTrending data={movie} />
                </SwiperSlide>
              );
            })}
          </Swiper>
        </div>
      </div>
    </section>
  );
};

export default TrendingSection;
