import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper";
import NavBar from "../Navigation/NavBar";
import "swiper/css";
import { useEffect, useState } from "react";
import { GetPopularApi } from "../../Api/GetApi";

const HeroSection = ({switchTheme}) => {
  const baseUrlImg = process.env.REACT_APP_BASE_URL_IMG;
  const [movieItems, setMovieItems] = useState([]);

  useEffect(() => {
    GetPopularApi().then((res) => {
      setMovieItems(res);
    });
  }, []);


  return (
    <section className="w-full h-fit overflow-y-auto">
      <NavBar page={"Home"} switchTheme={switchTheme} />
      <div className="w-full h-fit">
        <Swiper
          // slidesPerView={1}
          spaceBetween={0}
          autoplay={{
            delay: 4000,
          }}
          modules={[Autoplay]}
          className="mySwiper"
        >
          {movieItems.map((movie, i) => {
            return (
              <SwiperSlide key={i}>
                <div
                  className="w-full h-[600px] bg-cover bg-center relative"
                  style={{
                    backgroundImage: `url(${baseUrlImg}${movie.backdrop_path})`,
                  }}
                >
                  <div className={`from-gray-900 to-transparent dark:from-gray-400 dark:to-transparent z-[10] w-full h-full absolute top-0 left-0 bg-gradient-to-t `}>
                    <div className={`text-gray-200 w-full h-full flex flex-col justify-end items-center pb-[100px] px-5 leading-7`}>
                      <h1
                        className="text-[30px] font-rigthous text-center"
                        style={{ textShadow: "1px 1px 2px #93F2FF" }}
                      >
                        {movie.title}
                      </h1>
                      <p className="text-[10px] font-sans text-amber-500">
                        MOVIE POPULAR
                      </p>
                    </div>
                  </div>
                  <div className="w-full h-full absolute top-0 left-0 bg-gradient-to-b from-colorone to-transparent opacity-40"></div>
                  <div className="w-full h-full absolute top-0 left-0 bg-gradient-to-r from-colorone to-transparent opacity-40"></div>
                  <div className="w-full h-full absolute top-0 left-0 bg-gradient-to-l from-colorone to-transparent opacity-40"></div>
                </div>
              </SwiperSlide>
            );
          })}
        </Swiper>
      </div>
    </section>
  );
};

export default HeroSection;
