import { useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import { GetDetailMovie, GetMovieRecomendations } from "../Api/GetApi"
import CastDetailSection from "../Component/DetailMovie/CastDetailSection"
import Hero from "../Component/DetailMovie/Hero"
import MovieDetailSection from "../Component/DetailMovie/MovieDetailSection"
import RecomendationsSection from "../Component/DetailMovie/RecomendationsSection"
import hashids from "hashids";

const DetailMovie = () => {
  const {id} = useParams()
  const hashid = new hashids();
  const dec = hashid.decode(id)
  const [movieDetail, setMovieDetail] = useState([])

  useEffect(() => {
    GetDetailMovie(dec).then(res => {
      setMovieDetail(res)
    })
  }, [id])

  return (
    <main className="w-full h-screen overflow-hidden scrollbar md:scrollbar-thin md:scrollbar-track-gray-600 md:scrollbar-thumb-gray-500">
      <Hero data={movieDetail} />
      <MovieDetailSection data={movieDetail} />
      <CastDetailSection data={dec} />
      <RecomendationsSection data={dec} />
    </main>
  );
}

export default DetailMovie