import { Link, useNavigate } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAnglesRight,
  faVideo,
  faHouse,
  faPlay,
  faCircleInfo,
  faMagnifyingGlass,
  faSun,
  faMoon,
} from "@fortawesome/free-solid-svg-icons";
import { useEffect, useState } from "react";

const SideBarsNav = ({page, switchTheme}) => {
  const menu = [
    {
      title: "Home",
      logo: faHouse,
      to: ""
    },
    {
      title: "Movie",
      logo: faPlay,
      to: "discover"
    },
    {
      title: "About",
      logo: faCircleInfo,
      to : "about"
    },
    {
      title: "Search",
      logo: faMagnifyingGlass,
      to: "search"
    }
  ];

  const [open, setOpen] = useState(false);
  const navigate = useNavigate()
  
  const handleSwitchLight = () => {
    switchTheme("light")
  }
  
  const handleSwitchDark = () => {
    switchTheme("dark")
  }

  return (
    <div className="hidden md:block">
      <div
        className={`${
          open ? "w-[300px]" : "w-[80px]"
        } bg-colorone dark:bg-gray-300 duration-300 transition-all h-screen relative flex justify-center`}
      >
        <div className="h-full w-full text-gray-400 dark:text-gray-800 flex flex-col items-center">
          <div className="absolute z-50 top-[35px] right-[-30px] md:right-[-15px]">
            <div
              onClick={() => setOpen(!open)}
              className="bg-gray-100 text-gray-800 rounded-full p-2 flex items-center justify-center cursor-pointer"
            >
              <FontAwesomeIcon
                className={`${open && "rotate-180"} transition-all duration-300 text-[10px]`}
                icon={faAnglesRight}
              />
            </div>
          </div>
          <div className={`flex justify-center gap-3 items-center pt-7`}>
            <FontAwesomeIcon
              className={`${
                open ? "hidden" : "block"
              } bg-amber-600 text-colortwo text-lg p-3 rounded-full`}
              icon={faVideo}
            />
            <h1
              className={`${
                open ? "w-auto" : "scale-0 w-0"
              } duration-300 origin-left font-semibold text-gray-300 dark:text-gray-800 font-dmsans`}
            >
              Tkuz Movie
            </h1>
          </div>

          {/* Nav Side Menu */}
          <div className={`${open && "px-5"} mt-10 w-full h-full flex flex-col items-center justify-between`}>
            <div className="flex flex-col w-full">
              <h1 className="text-lg font-semibold my-3 mx-4">Menu</h1>
              {menu.map((nav, i) => {
                return (
                  <div
                    key={i}
                    onClick={() => navigate(`/${nav.to}`)}
                    className={`${open && `hover:bg-gray-900 pr-16 ${page === nav.title && "bg-gray-900"}`} flex justify-center duration-300 py-3 mb-5 rounded-md group gap-2 items-center cursor-pointer`}
                  >
                    <div className={`${page === nav.title ? "bg-amber-600" : "bg-colortwo"} w-[45px] h-[45px] flex justify-center items-center dark:bg-gray-400  group-hover:bg-amber-600 duration-300 text-colorone text-lg rounded-full`}>
                      <FontAwesomeIcon className="" icon={nav.logo} />
                    </div>
                    <h1
                      className={`${
                        open ? "w-auto" : "scale-0 w-0"
                      } duration-300  origin-left font-semibold cursor-pointer`}
                    >
                      {nav.title}
                    </h1>
                  </div>
                );
              })}
            </div>
            <div className="w-fit bg-colortwo dark:bg-gray-400 flex flex-col items-center justify-center mb-10 rounded-full">
              <div onClick={handleSwitchLight} className="text-colorone dark:text-amber-600 w-[45px] h-[45px] flex justify-center items-center  hover:text-amber-600  duration-300  text-lg rounded-full">
                <FontAwesomeIcon icon={faSun} />
              </div>
              <div onClick={handleSwitchDark} className="text-amber-600 dark:text-colorone w-[45px] h-[45px] flex justify-center items-center hover:text-amber-600 duration-300 text-lg rounded-full">
                <FontAwesomeIcon icon={faMoon} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SideBarsNav;
