import AboutSection from "../Component/About/AboutSection";
import SideBarsNav from "../Component/Navigation/SideBarsNav";

const About = ({ switchTheme }) => {
  return (
    <main className="w-full h-screen overflow-auto flex">
      <header>
        <SideBarsNav page={"About"} switchTheme={switchTheme} />
      </header>
      <section className="w-full h-screen overflow-auto bg-colorone scroll-smooth scrollbar md:scrollbar-thin md:scrollbar-track-gray-600 md:scrollbar-thumb-gray-500">
        <AboutSection />
      </section>
    </main>
  );
};

export default About