import HeroSection from "../Component/Home/HeroSection";
import NowPlayingSection from "../Component/Home/NowPlayingSection";
import PopularSection from "../Component/Home/PopularSection";
import TrendingSection from "../Component/Home/TrendingSection";
import SideBarsNav from "../Component/Navigation/SideBarsNav";

const Home = ({switchTheme}) => {
  return (
    <main>
      <section className="flex w-full overflow-auto">
        <header className="w-fit">
          <SideBarsNav page={"Home"} switchTheme={switchTheme} />
        </header>
        <div className="w-full h-screen overflow-hidden scroll-smooth scrollbar md:scrollbar-thin md:scrollbar-track-gray-600 md:scrollbar-thumb-gray-500">
          <HeroSection switchTheme={switchTheme} />
          <NowPlayingSection  />
          <TrendingSection />
          <PopularSection />
        </div>
      </section>
    </main>
  );
};

export default Home;
