import { faGithub, faInstagram, faLinkedin } from "@fortawesome/fontawesome-free-brands";
import { faCaretDown, faCaretUp } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState } from "react";
import NavBar from "../Navigation/NavBar";
import "./About.css"

const AboutSection = () => {
    const [show, setShow] = useState(null);

    const togle = (i) => {
      if (show === i) {
        return setShow(null);
      }

      setShow(i);
    };

  return (
    <div>
      <NavBar />
      <div className="w-full h-fit flex flex-col items-center px-10 pt-[150px]">
        {dataQuestion.map((item, i) => (
          <div className="w-full md:w-[700px] mb-4">
            <div
              onClick={() => togle(i)}
              className="mb-1 text-[18px] flex justify-between items-center"
            >
              <h1 className="text-gray-400">{item.question}</h1>
              <FontAwesomeIcon
                className="text-gray-400"
                icon={show === i ? faCaretDown : faCaretUp}
              />
            </div>
            <div
              className={`${
                show === i ? "content show" : "content"
              } overflow-hidden mb-2 text-[18px]`}
            >
              <p className="text-gray-500">{item.answer}</p>
            </div>
            <hr />
          </div>
        ))}
        <div className="text-gray-400 text-[30px] flex gap-5 mt-10">
          <a href="https://github.com/Tamakuz">
            <FontAwesomeIcon
              className="hover:text-gray-500 cursor-pointer duration-300"
              icon={faGithub}
            />
          </a>
          <a href="https://www.instagram.com/jovantama_/">
            <FontAwesomeIcon
              className="hover:text-gray-500 cursor-pointer duration-300"
              icon={faInstagram}
            />
          </a>
          <a href="https://www.linkedin.com/in/jovan-panji-pratama-b5a005262/">
            <FontAwesomeIcon
              className="hover:text-gray-500 cursor-pointer duration-300"
              icon={faLinkedin}
            />
          </a>
        </div>
      </div>
    </div>
  );
}

const dataQuestion = [
  {
    question: "What is TkuzMOVIE?",
    answer:
      "Just like every other website, this website is also a streaming site that helps to easily access all the TV shows and movies we wanted, without spending hours searching for them.",
  },
  {
    question: "So what do we actually do?",
    answer:
      "Well, let me tell you what we dont do: we definitely dont illegally host our files. We do not store any copyright-protected content on our website. Any linked content is stored only in third-party websites. This is a promotional website only. All files placed here are for introducing purpose. We highly ENCOURAGE users to BUY the CDs or DVDs of the movie or the music they like.",
  },
  {
    question: "I cannot watch video because of ads",
    answer:
      "We are very sorry that we can't help you with that. We have no control in the ads being served. Don't download anything in the popups. If you don't want to be annoyed. We highly recommend subscribing to a legal streaming service that you can afford.",
  },
  {
    question: "Streaming speed is slow or all videos do not play",
    answer:
      "When you go on the page with the episode, in 99% of the cases there is a video player. What you have to do is click the Play button, of course. If it does not work (Dont be judgmental! Everybody makes mistakes!), just click on the Servers you see on the top left of your device. You will get a list of servers [Vidcloud, Hydrax etc.] Try choosing different server, it will definitely solve the problem.",
  },
  {
    question: "I want to download video",
    answer:
      "Since we don't store any files, so we don't have any download feature here. All files found on this site have been collected from various sources across the web and are believed to be in the public domain.",
  },
  {
    question: "Is it safe to stream in this website?",
    answer:
      "This website is undoubtedly safer to stream, however downloading, uploading is illegal. You will not get into any trouble while using our website. It's highly not recommended to download the files and share them to the public, It might get you in trouble.",
  },
];

export default AboutSection