import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircle, faStar } from "@fortawesome/free-solid-svg-icons";
import { useNavigate } from "react-router-dom";
import hashids from "hashids";

const CardDiscover = ({ data }) => {
  const navigate = useNavigate();
  const hashid = new hashids();
  const baseUrlImg = process.env.REACT_APP_BASE_URL_IMG_500;
  const title = data.title;
  const enc = hashid.encode(data.id);

  return (
    <div
      onClick={() => navigate(`/detail/${enc}`)}
      className="w-[160px] h-fit pt-4"
    >
      <div className="hover:scale-110 hover:brightness-75 duration-300 ease-in-out">
        <img
          className="w-full rounded-2xl"
          src={`${baseUrlImg}${data.poster_path}`}
          alt=""
        />
      </div>
      <div className="text-gray-300 dark:text-gray-800 text-[17px] font-dmsans mt-2 px-1">
        <h1>{title.substring(0, 160 / 10)}...</h1>
      </div>
      <div className="flex items-center gap-1 text-[14px] px-1 mt-1">
        <p className="text-gray-500">{data.release_date.substring(0, 4)}</p>
        <FontAwesomeIcon className="text-[5px] text-gray-400" icon={faCircle} />
        <p className="text-gray-500">
          {Math.floor(data.vote_average * 10) / 10}
        </p>
        <FontAwesomeIcon className="text-[10px] text-amber-300" icon={faStar} />
      </div>
    </div>
  );
};

export default CardDiscover;
