import {
  faBars,
  faHouse,
  faPlay,
  faCircleInfo,
  faMagnifyingGlass,
  faXmark,
  faMoon,
  faSun
} from "@fortawesome/free-solid-svg-icons";
import { useNavigate } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState } from "react";

const NavBar = ({page, switchTheme}) => {

  const menu = [
    {
      title: "Home",
      logo: faHouse,
      to: ""
    },
    {
      title: "Movie",
      logo: faPlay,
      to: "discover"
    },
    {
      title: "About",
      logo: faCircleInfo,
      to: "About"
    },
  ];

  const navigate = useNavigate()
  const [open, setOpen] = useState(false);

  const handleSwitchLight = () => {
    switchTheme("light");
  };

  const handleSwitchDark = () => {
    switchTheme("dark");
  };


  return (
    <nav className="w-full h-[70px] block md:hidden absolute z-10">
      <div className={`duration-300 h-full w-full px-[30px] bg-gray-800 dark:bg-gray-300`}>
        <div className="flex h-full items-center">
          <div
            onClick={() => setOpen(!open)}
            className="block md:hidden text-gray-400 dark:text-gray-800 text-xl w-full"
          >
            <FontAwesomeIcon icon={open ? faXmark : faBars} />
          </div>
          <div className="w-full  flex gap-5 items-center justify-end">
            <div onClick={() => navigate("/search")} className="bg-gray-900 dark:bg-gray-400 text-gray-400 dark:text-gray-800 hover:text-gray-300 w-[30px] h-[30px] flex justify-center items-center p-5 rounded-full text-[14px] font-bold">
              <FontAwesomeIcon icon={faMagnifyingGlass} />
            </div>
            <div className="w-fit bg-gray-700 dark:bg-gray-400 flex items-center justify-center rounded-full">
              <div
                onClick={handleSwitchLight}
                className="text-gray-800 dark:text-amber-600 w-[30px] h-[30px] flex justify-center items-center  hover:text-amber-600  duration-300  text-md rounded-full"
              >
                <FontAwesomeIcon icon={faSun} />
              </div>
              <div
                onClick={handleSwitchDark}
                className="text-amber-600 dark:text-gray-800 w-[30px] h-[30px] flex justify-center items-center hover:text-amber-600 duration-300 text-md rounded-full"
              >
                <FontAwesomeIcon icon={faMoon} />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        className={`${
          open ? "top-[70px]" : "top-[-500px]"
        } block md:hidden left-0 bg-gray-800 w-full h-fit absolute transition-all duration-300 z-[-1]`}
      >
        <div className="flex flex-col justify-center w-full items-center py-5">
          {menu.map((nav, i) => {
            return (
              <div
                onClick={() => navigate(`/${nav.to}`)}
                key={i}
                className={`${
                  page === nav.title ? "bg-gray-900" : ""
                } text-gray-400 w-fit hover:bg-gray-900 hover:text-gray-300 px-[60px] py-[15px]  my-3 rounded-full font-semibold duration-300 cursor-pointer`}
              >
                <FontAwesomeIcon icon={nav.logo} /> {nav.title}
              </div>
            );
          })}
        </div>
      </div>
    </nav>
  );
};

export default NavBar;
