import { useEffect, useState } from "react";
import { GetSearchMovie } from "../Api/GetApi";
import SideBarsNav from "../Component/Navigation/SideBarsNav"
import CardSearch from "../Component/Search/CardSearch";
import SearchSection from "../Component/Search/SearchSection"

const Search = ({ switchTheme }) => {
  const [value, setValue] = useState("")
  const [movies, setMovies] = useState([])
  const baseUrlImg = process.env.REACT_APP_BASE_URL_IMG_500;

  useEffect(() => {
    GetSearchMovie(value).then(res => {
      setMovies(res)
    })
  }, [value])

  return (
    <main>
      <section className="flex w-full h-screen overflow-auto">
        <header>
          <SideBarsNav page={"Search"} switchTheme={switchTheme} />
        </header>
        <main className="w-full h-screen bg-colorone relative overflow-auto scroll-smooth scrollbar md:scrollbar-thin md:scrollbar-track-gray-600 md:scrollbar-thumb-gray-500">
          <SearchSection value={(value) => setValue(value)} />
          <div className="w-full h-screen pt-[80px] md:px-[30px] flex flex-wrap justify-center gap-3">
            {movies ? (
              movies.map((movie, i) => {
                return <CardSearch key={i} data={movie && movie} />;
              })
            ) : (
              <h1 className="text-gray-600 mt-10">
                Type query to start search..
              </h1>
            )}
          </div>
        </main>
      </section>
    </main>
  );
};

export default Search