const CardCast = ({data}) => {
  const baseUrlImg = process.env.REACT_APP_BASE_URL_IMG_500;

  return (
    <div className="w-[160px] h-fit text-gray-500">
      <div>
        <img
          className="rounded-xl"
          src={`${baseUrlImg}${data.profile_path && data.profile_path}`}
          alt="belum punya foto"
        />
      </div>
      <div className="w-full text-center py-3 leading-4">
        <h1 className="text-[16px] font-bold text-gray-400">{data.name}</h1>
        <p className="text-[14px] font-thin">{data.character}</p>
      </div>
    </div>
  );
}

export default CardCast