import { useEffect, useState } from "react"
import { GetDiscover, GetGenres } from "../Api/GetApi"
import CardDiscover from "../Component/Discover/CardDiscover"
import NavBar from "../Component/Navigation/NavBar"
import SideBarsNav from "../Component/Navigation/SideBarsNav"

const Discover = ({switchTheme}) => {
  const [movies, setMovies] = useState([])
  const [genres, setGenres] = useState([])
  const [selected, setSelected] = useState(28)

  useEffect(() => {
    GetDiscover(selected).then(res => {
      setMovies(res)
    })
  }, [selected])

  useEffect(() => {
    GetGenres().then(res => {
      setGenres(res)
    })
  }, [])

  return (
    <main>
      <section className="flex w-full h-screen overflow-auto">
        <header>
          <SideBarsNav page={"Movie"} switchTheme={switchTheme} />
        </header>
        <main className="w-full h-screen bg-colorone relative overflow-auto scroll-smooth scrollbar md:scrollbar-thin md:scrollbar-track-gray-600 md:scrollbar-thumb-gray-500">
          <NavBar page={"Movie"} switchTheme={switchTheme} />
          <h1 className="block w-full text-[30px] font-bold text-gray-400 px-[50px] pt-[90px] pb-5 md:py-10">
            Movie Discover
          </h1>
          <div className="w-full h-fit flex flex-wrap justify-center md:justify-start pb-5 px-2 md:px-[90px] gap-5">
            {genres &&
              genres.map((genre, i) => {
                return (
                  <div
                    onClick={() => setSelected(`${genre.id}`)}
                    className={`${
                      selected == genre.id && "bg-amber-600"
                    } bg-gray-600 px-5 py-2 rounded text-colorone hover:bg-amber-600 duration-300 cursor-pointer`}
                  >
                    <p>{genre.name}</p>
                  </div>
                );
              })}
          </div>
          <div className="w-full h-screen md:px-[30px] flex flex-wrap justify-center gap-4">
            {movies &&
              movies.map((movie, i) => {
                return <CardDiscover data={movie} />;
              })}
          </div>
        </main>
      </section>
    </main>
  );
}

export default Discover