import { HashRouter, Route, Routes } from "react-router-dom";
import Home from "./Pages/Home";
import Movie from "./Pages/Movie";
import { useEffect, useState } from "react";
import DetailMovie from "./Pages/DetailMovie";
import Search from "./Pages/Search";
import About from "./Pages/About";
import Discover from "./Pages/Discover";

function App() {
  const [theme,setTheme] = useState("dark")

  useEffect(() => {
    if(theme === "dark") {
      document.documentElement.classList.add("light")
      document.documentElement.classList.remove("dark")
    }else{
      document.documentElement.classList.remove("light");
      document.documentElement.classList.add("dark");
    }
  }, [theme])

  const handleSwitchTheme = (value) => {
    setTheme(value)
  }
  
  return (
    <HashRouter>
      <Routes>
        <Route
          path="/"
          element={<Home switchTheme={(value) => handleSwitchTheme(value)} />}
        ></Route>
        <Route path="/detail/:id" element={<DetailMovie />} />
        <Route path="/:movie" element={<Movie />} />
        <Route
          path="/search"
          element={<Search switchTheme={(value) => handleSwitchTheme(value)} />}
        />
        <Route
          path="/about"
          element={<About switchTheme={(value) => handleSwitchTheme(value)} />}
        />
        <Route
          path="/discover"
          element={
            <Discover switchTheme={(value) => handleSwitchTheme(value)} />
          }
        />
      </Routes>
    </HashRouter>
  );
}

export default App;
